body {
  margin: 0;
  font-family: sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  color: rgba(255, 0, 0, 0.425);
}

.button
{
    min-width: 100px;
    min-height: 50px;
    font-weight: bolder;

    display: flex;
    justify-content: center;
    align-items: center;
    
    border: none;
    background-color: rgba(20, 20, 20, 0.05);
    resize: none;
    outline: none;
}
.button:hover
{
    background-color: rgba(20, 20, 20, 0.1);
}
.button:active
{
    background-color: rgba(20, 20, 20, 0.2);
}

.buttonred
{
    background-color: rgba(255, 0, 0, 0.2);
}
.buttonred:hover
{
    background-color: rgba(255, 0, 0, 0.3);
}
.buttonred:active
{
    background-color: rgba(255, 0, 0, 0.4);
}

.buttongreen
{
    background-color: rgba(114, 250, 193, 0.3);
}
.buttongreen:hover
{
    background-color: rgba(114, 250, 193, 0.4);
}
.buttongreen:active
{
    background-color: rgba(114, 250, 193, 0.5);
}

.buttonfill
{
    width: 100%;
    height: 100%;
}

input
{
    border: none;
    background-color: transparent;
    resize: none;
    outline: none;

    width: calc(100% - 8px);
    height: 100%;
    text-align: center;
    font-size: larger;
}
.upperContainer
{
    border: 2px solid black;
}

.calculator
{
    padding: 20px;
}

